body {
    box-sizing: border-box;
    background-color: #2A2438;
    margin: 0;
    padding: 0;
    font-family: cursive;
}

html {
    font-size: 62.5%;
}

.container {
    font-family: cursive;
    display: grid;
    place-items: center;
    height: 100vh;
}



.to-do {
    background-color: whitesmoke;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 1rem;
    justify-items: center;
    box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.5);
}

.title {
    color: #663A82;

    font-size: 4rem;
}

button {
    background-color: #7C5295;
    padding: 1rem;
    font-size: 2.5rem;
    border: 2px solid #ccc;
    border-radius: 4px;
    margin-left: 1rem;
}

.task {
    background-color: #BCA0DC;
    padding: 1rem;
    font-size: 2rem;
    list-style: none;
    margin-bottom: 1rem;
    border-radius: 1rem;
    box-shadow: 5px 5px 10px #7C5295;
    width: 27rem;
}

input {
    padding: 1rem;
    font-size: 1.5rem;
    background-color: transparent;
    border: none;
    border: 1px solid #663A82;
    border-radius: 2px;
    outline: none;
}